import './App.css';
import { React, useState, useEffect } from 'react';
import { Duck } from './Duck';
import { Bread } from './Bread';



function App() {

  const player = { x: 0, y: 0 };

  const [angle, setAngle] = useState(0);
  const [ducks, setDucks] = useState([
    { origin: { x: 0, y: -800 }, x: 0, y: -800, target: player, speed: 50, hasBread: false, hasExited: false }
  ]);
  const [breads, setBreads] = useState([
    { angle: .2 - Math.PI / 2, speed: 200, r: 0, age: 0, isTaken: false }
  ]);
  const [isTouching, setIsTouching] = useState(false);
  const [throwPower, setThrowPower] = useState(0);

  const [tick, setTick] = useState(0);

  const addDuck = (ducks) => {
    const angle = Math.random() * 2 * Math.PI;
    const newDucks = JSON.parse(JSON.stringify(ducks));
    const x = 800 * Math.cos(angle);
    const y = 800 * Math.sin(angle);
    newDucks.push({
      x: x,
      y: y,
      origin: { x: x, y: y },
      target: player,
      speed: Math.floor(10 + Math.random() * 50)
    });
    return newDucks
  }

  const updateTime = (ducks, setDucks) => {
    setTick(tick + 1);

    let newBreads = JSON.parse(JSON.stringify(breads));
    for (let bread of newBreads) {
      bread.x = bread.r * Math.cos(bread.angle);
      bread.y = bread.r * Math.sin(bread.angle);
      bread.r += bread.speed;
      bread.speed = Math.max(0, bread.speed * .5 - 1);
      bread.age += 1;
    }



    if (isTouching) {
      setThrowPower(throwPower + 1);
    }
    let newDucks = JSON.parse(JSON.stringify(ducks));
    newDucks = newDucks.filter(duck => (
      (duck.target.x !== duck.origin.x || duck.target.y !== duck.origin.y || ((duck.x ** 2 + duck.y ** 2) ** .5 <= 700)))
    );

    for (let duck of newDucks) {
      let minDist = 100;
      for (let bread of newBreads) {
        let dx = duck.x - bread.x;
        let dy = duck.y - bread.y;
        let dist = (dx ** 2 + dy ** 2) ** .5;
        if ((dist < minDist) & (!duck.hasBread)) {
          minDist = dist;
          duck.target = bread;
        }
        if (dist < 40) {
          duck.hasBread = true;
          bread.isTaken = true;
          duck.target = duck.origin;
          duck.speed = 50;
        }
      }
    }
    newBreads = newBreads.filter(bread => (bread.age <= 100 && !bread.isTaken));
    setBreads(newBreads);
    setDucks(newDucks);
    if ((tick % 4) !== 0) {
      return
    }
    if (!(tick % 100)) {
      newDucks = addDuck(ducks);
    }
    for (let duck of newDucks) {
      let dx = duck.target.x - duck.x;
      let dy = duck.target.y - duck.y;
      let xRatio;
      if (dx === 0) {
        xRatio = 0;
      } else if (dy === 0) {
        xRatio = 1;
      } else {
        xRatio = Math.abs(dx) / (Math.abs(dx) + Math.abs(dy));
      }
      duck.x += Math.sign(dx) * duck.speed * xRatio;
      duck.y += Math.sign(dy) * duck.speed * (1 - xRatio);
    }
    setDucks(newDucks);
  }

  useEffect(() => {
    const interval = setInterval(() => {
      updateTime(ducks, setDucks);
    }, 100);

    return () => {
      clearInterval(interval);
    };
  }, [ducks, tick, updateTime]); // has no dependency - this will be called on-component-mount


  useEffect(() => {
    window.addEventListener('deviceorientation', event => {
      setAngle(event.alpha);
      // let transform = document.getElementById('rotate');
      // transform.setAttribute('transform', `rotate (${event.alpha} 200 700)`);
    });

    setAngle(0);

  }, []);

  const onKeyDown = event => {
    if (event.key === 'a') {
      setAngle(angle + 4);
    }
    if (event.key === 'd') {
      setAngle(angle - 4);
    }
  }

  const onMouseDown = event => {
    setIsTouching(true);
    setThrowPower(0);
  }

  const onMouseUp = event => {
    setIsTouching(false);
    if (angle === null) {
      setAngle(0);
    }
    if (throwPower < 1) {
      return
    }
    // console.log('throw', throwPower, angle);
    const newBreads = JSON.parse(JSON.stringify(breads));
    newBreads.push(
      { x: player.x, y: player.y, angle: (270 - angle) * Math.PI / 180, speed: throwPower * 50, r: 0, age: 0, label: Math.random().toString(36).substring(7) }
    );
    setBreads(newBreads);
  }

  const getAngle = (duck) => {
    let dx = duck.target.x - duck.x;
    let dy = duck.target.y - duck.y;
    return (180 / Math.PI) * Math.atan2(dy, dx);
  }

  return (
    <div className="App">
      <header className="App-header">
        {/* <p>{throwPower}</p> */}
        <svg height="700" viewBox="-150 -700 300 700" width="400" tabIndex="0" onKeyDown={onKeyDown} onTouchStart={onMouseDown} onTouchEnd={onMouseUp} style={{ border: "solid 1px green" }}>
          <rect x="90" y="-690" height="100" width="100" style={{ fill: "rgb(255 255 255)", fillOpacity: 0.2 }} />
          <g transform={`rotate (${angle} ${140 + player.x * 100 / 1400} ${-640 + player.y * 100 / 1400})`}>
            {
              ducks.map((duck, i) => {
                return <circle key={i} cx={140 + duck.x * 100 / 1400} cy={-640 + duck.y * 100 / 1400}
                  fill={`rgb(${duck.speed * 40} ${100 - 8 * duck.speed} ${150 - duck.speed * 30})`}

                  r={20 * 100 / 700} />
              })
            }
            <circle cx={140 + player.x * 100 / 1400} cy={-640 + player.y * 100 / 1400} fill="green" r={20 * 100 / 1400} />
          </g>
          <g transform={`rotate (${angle} ${player.x} ${player.y})`}>
            <circle cx={player.x} cy={player.y} r={5} fill="green" />
            {
              breads.map((bread, i) => {
                // return <circle key={i} cx={bread.x} cy={bread.y} r="5" fill="orange"/>
                return <Bread key={i} x={bread.x} y={bread.y} age={bread.age} />
              })
            }

            {
              ducks.map((duck, i) => {
                return <Duck key={i} x={duck.x} y={duck.y} speed={duck.speed} hasBread={duck.hasBread} angle={270 + getAngle(duck) + ((Math.floor(tick / 4) % 2) ? - 5 : 5)} />
                // return <circle key={i} cx={duck.x} cy={duck.y}
                // fill={`rgb(${duck.speed * 40} ${100 - 8 * duck.speed} ${150 - duck.speed * 30})`}
                // r="10" />
              })
            }
          </g>
        </svg>
      </header>
    </div>
  );
}

export default App;
